<template>

  <div class="camera_outer">
    <Icon name="back" class="back" @click.native="goBack"/>
    <video
        id="videoCamera"
        autoplay></video>
    <canvas
        style="display: none;"
        id="canvasCamera"
        :width="videoWidth"
        :height="videoHeight"></canvas>
    <div v-if="imgSrc" class="img_bg_camera">
      <img :src="imgSrc" alt="" class="tx_img"/>
    </div>
    <!--      <button @click="getCompetence()">打开摄像头</button>-->
    <!--      <button @click="stopNavigator()">关闭摄像头</button>-->
    <button @click="setImage();stopNavigator(); search()">
      拍照
    </button>
    <!--      <button @click="store">入库</button>-->
    <!--      <button @click="search">检索</button>-->
  </div>


</template>

<script>
import {urlEncode} from '@/utils/urlEncode.js'

export default {
  name: 'CameraTest',
  data() {
    return {
      videoWidth: 300,
      videoHeight: 300,
      imgSrc: '',
      thisCanvas: null,
      thisContext: null,
      thisVideo: null,
    }
  },
  mounted() {
    this.getCompetence()
    if (window.history && window.history.pushState) {
      // history.pushState(null, null, document.URL)
      window.addEventListener('popstate', this.stopNavigator)
    }
  },
  methods: {
    // 调用权限（打开摄像头功能）
    getCompetence() {
      const _this = this
      this.thisCanvas = document.getElementById('canvasCamera')
      this.thisContext = this.thisCanvas.getContext('2d')
      this.thisVideo = document.getElementById('videoCamera')
      // 旧版本浏览器可能根本不支持mediaDevices，我们首先设置一个空对象
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {}
      }
      // 一些浏览器实现了部分mediaDevices，我们不能只分配一个对象
      // 使用getUserMedia，因为它会覆盖现有的属性。
      // 这里，如果缺少getUserMedia属性，就添加它。
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          // 首先获取现存的getUserMedia(如果存在)
          const getUserMedia =
              navigator.webkitGetUserMedia ||
              navigator.mozGetUserMedia ||
              navigator.getUserMedia
          // 有些浏览器不支持，会返回错误信息
          // 保持接口一致
          if (!getUserMedia) {
            return Promise.reject(
                new Error('getUserMedia is not implemented in this browser')
            )
          }
          // 否则，使用Promise将调用包装到旧的navigator.getUserMedia
          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject)
          })
        }
      }
      const constraints = {
        audio: false,
        video: {
          width: this.videoWidth,
          height: this.videoHeight,
          facingMode: 'environment',
        },
      }
      navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function (stream) {
            // 旧的浏览器可能没有srcObject
            if ('srcObject' in _this.thisVideo) {
              _this.thisVideo.srcObject = stream
            } else {
              // 避免在新的浏览器中使用它，因为它正在被弃用。
              _this.thisVideo.src = window.URL.createObjectURL(stream)
            }
            _this.thisVideo.onloadedmetadata = function (e) {
              _this.thisVideo.play()
            }
          })
          .catch((err) => {
            console.log(err)
          })
    },
    //  绘制图片（拍照功能）
    setImage() {
      const _this = this
      // 点击，canvas画图
      _this.thisContext.drawImage(
          _this.thisVideo,
          0,
          0,
          _this.videoWidth,
          _this.videoHeight
      )
      // 获取图片base64链接
      const image = this.thisCanvas.toDataURL('image/png')
      _this.imgSrc = image
      this.$emit('refreshDataList', this.imgSrc)
    },
    // base64转文件
    dataURLtoFile(dataUrl, filename) {
      const arr = dataUrl.split(',')
      const mime = arr[0].match(/:(.*?);/)[1]
      const bstr = atob(arr[1])
      let n = bstr.length
      const u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new File([u8arr], filename, {type: mime})
    },
    // 关闭摄像头
    stopNavigator() {
      this.thisVideo.srcObject.getTracks()[0].stop()
    },

    store() {
      // const url = 'https://aip.baidubce.com/rest/2.0/realtime_search/same_hq/add'
      const bashUrl = 'https://aip.baidubce.com/rest/2.0/image-classify/v1/realtime_search/similar/add'
      const brief = '哈哈哈哈----'
      // 删除data:image/png;base64,
      const urlDataRaw = this.thisCanvas.toDataURL('image/png').slice(22)
      const urlData = 'image=' + urlEncode(urlDataRaw) + '&brief=' + brief
      const accessToken = '24.af28718029713fd2397bec9d0f561e9f.2592000.1657244688.282335-26392949'
      // const addQuery = 'https://aip.baidubce.com/rest/2.0/realtime_search/same_hq/add?access_token=' + accessToken
      const url = bashUrl + '?access_token=' + accessToken
      const option = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: urlData,
      }
      fetch(url, option)
          .then((res) => {
            return res.json()
          })
          .then((res) => {
            console.log(res)
          })

      // async function fetchData() {
      //   const res = await fetch(url, option)
      //   const resJson = await res.json()
      //   console.log(resJson)
      // }

    },

    search() {
      const bashUrl = 'https://aip.baidubce.com/rest/2.0/image-classify/v1/realtime_search/similar/search'
      const access_Token = '24.af28718029713fd2397bec9d0f561e9f.2592000.1657244688.282335-26392949'
      const url = `${bashUrl}?access_token=${access_Token}`
      const urlDataRaw = this.thisCanvas.toDataURL('image/png').slice(22)
      const urlData = `image=${urlEncode(urlDataRaw)}`
      const option = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: urlData,
      }

      fetch(url, option)
          .then(res => res.json())
          .then(res => {
            const brief = res.result[0].brief
            const id = parseInt(brief.slice(-1)) - 1
            const path = `/detail/${id}`
            this.$router.push(path)
          })

      // const that = this
      // async function fetchData() {
      //   const res = await fetch(url, option)
      //   const resJson = await res.json()
      //   const brief = resJson.result[0].brief
      //   截取数字
      // const sceneNum = brief.substring(1, brief.indexOf('场'))
      // 跳转到相应的场景 例如 第十二场 路径为 category/2-1
      // const scene = dict[sceneNum]
      // const path = `/category/${scene}`
      // await that.$router.push(path)
      // }
      // fetchData()

    },

    goBack() {
      this.$router.go(-1)
    },
  },
  destroyed() {
    window.removeEventListener('popstate', this.stopNavigator, false) //false阻止默认事件
  }
}
</script>

